@media (max-width: 1200px) {
    .container {
        max-width: 1140px;
    }
}

@media (max-width: 992px) {
    .container {
        max-width: 960px;
    }
    .gap-5 {
        gap: 2.5rem;
    }
    .description {
        width: 80%;
        margin: 0 auto 2.5rem;
    }
}

@media (max-width: 768px) {
    .container {
        max-width: 720px;
    }
    .big-card {
        height: 550px;
    }
    .App {
        flex-direction: column-reverse;
    }
    nav {
        width: 100%;
        flex-direction: row;
        height: auto;
        padding: 1.5rem 1.5rem 1.5rem 1.5rem;
    }
    nav ul {
        flex-direction: row;
    }
    .ai-component {
        flex-direction: column;
        gap: 1rem;
    }
    .main {
        height: 100%;
    }
    .big-card {
        min-width: 100%;
        height: auto;
        gap: 10px;
        min-height: 370px;
        justify-content: center;
    }
    .big-card .column {
        gap: 10px;
    }
    .big-card img {
        width: 15rem;
    }
    nav .icon-button {
        height: 40px;
        width: 40px;
    }
    .logo {
        height: 50px;
    }
}

@media (max-width: 576px) {
    .container {
        max-width: 540px;
    }
    .mainLogo {
        font-size: 24px;
        gap: 4px;
    }
    .mainLogo img{
        height: 60px;
    }
    .big-card img {
        max-height: 80%;
    }
    .button-group {
        gap: 0.5rem;
    }
    .button-group .button {
        justify-content: center;
    }
    .button, .button-register {
        width: 100%;
        padding: 8px;
    }
    .notification {
        top: 10px;
        right: 0;
        left: 0;
        max-width: 280px;
        height: fit-content;
    }
    .text-center {
        text-align: center;
        font-size: 14px;
    }
    .description {
        width: 100%;
        font-size: 14px;
    }
    .select-box {
        width: 100%;
    }
    .custom-select {
        width: 100%;
        margin-bottom: 10px;
    }
    .notification a {
        font-size: 14px;
    }
    nav {
        padding: 0.5rem;
    }
    .logo {
        display: none;
    }
    .about-info .logo {
        display: block;
    }
    nav ul {
        gap: 0;
        justify-content: space-between;
        width: 78%;
    }
    .profile-header {
        gap: 1.5rem;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }
    input[type=text], input[type=password], input[type=time], input[type=date], input[type=datetime-local], input[type=datetime], input[type=month], input[type=week], input[type=email], input[type=number], input[type=tel], input[type=search], input[type=url] {
        width: 80vw;
    }
    .tab-layout {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

@media (max-width: 320px) {
    .container {
        width: 100%;
    }
    .mainLogo {
        font-size: 20px;
    }
    .brief-card {
        width: 90vw;
    }
}